@import url(https://fonts.googleapis.com/css?family=Bangers|Shadows+Into+Light+Two&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
}

.App-logo {
  height: 5vh;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    color: #fff;
  }
}

.App-footer {
  background-color: #808080;
  height: 8vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.credits a,
.credits {
  font-size: 0.95em;
  height: 5%;
  text-decoration: none;
  border: none;
  color: #fff;
  padding-bottom: 1em;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #808080;
}

.app {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-family: "Tahoma", sans-serif;
  background-color: #808080;
  transition: all 0.5s ease;
}

.container {
  height: 100%;
  padding: 2em;
  width: 70%;
  background-color: #808080;
  color: #fff;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}

h1 {
  margin: 1em auto 1em auto;
  text-align: center;
  font-size: 1.5em;
}

.text {
  min-height: 5.5em;
  display: flex;
  align-items: center;
  font-size: 2.5em;
  font-weight: 400;
  font-family: "Bangers", cursive;
}
#author {
  text-align: right;
  font-size: 1.5em;
  font-family: "Shadows Into Light Two", "Times New Roman", cursive;
}

.buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1em;
}

button,
button:focus {
  padding: 0em 0.5em;
  border: none;
  border-radius: 4px;
  height: 1.8em;
  cursor: pointer;
  background-color: #fff;
  color: #808080;
  font-weight: 600;
}
button:hover {
  background-color: black;
  color: #fff;
}
button:active {
  background-color: #fff;
  color: #808080;
}

.btn {
  border: none;
  display: inline-block;
  background-color: #1b95e0;
  color: #fff;
  cursor: pointer;
  height: 1.8em;
  border-radius: 4px;
  padding: 0.1em 0.5em;
  font-weight: 600;
}

.btn:hover {
  background-color: black;
}

.label {
  border: none;
  font-family: "Helvetica Neue", Arial, sans-serif;
  font-size: 13px;
  line-height: 26px;
  display: inline-block;
  margin: 0 0 0 4px;
}
